/**
 * External dependencies
 */
import { Button, Icon } from '@wordpress/components';
import { copy } from '@wordpress/icons'

import './style.scss';

const ClickableList = ( { items, onClick } ) => {
	const formatText = ( type, text ) => {
		switch ( type ) {
			case 'component':
				return `<${ text } />`;
			case 'object':
				return `{${ text }}`;
			default:
				return `${ text }()`;
		}
	};

	return (
		<ul className="gutenbuild-clickable-list">
			{ items.map( ( i ) => {
				const split = i.text.split( '.' );
				const text = split.pop();

				return (
					<li key={ i.text }>
						<a href="#" onClick={ () => onClick( i ) }>
							<div className="gutenbuild-clickable-list__title">
								{ formatText( i.type, text ) }
							</div>
							{
								<span
									key={ i }
									className="gutenbuild-clickable-list__subtitle"
								>
									{ split.join( '.' ) }
								</span>
							}
							<div className="gutenbuild-clickable-list__tools">
								<Button isSmall={ true } isPrimary={ true }>
									Copy
								</Button>
							</div>
						</a>
					</li>
				);
			} ) }
		</ul>
	);
};

export default ClickableList;
